@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@400;900&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

html,
body {
  padding: 0;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen,
  Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
}

a {
  color: inherit;
  text-decoration: none;
}

* {
  box-sizing: border-box;
  font-family: 'Nunito', sans-serif;
}

.shadow-light {
  /*border-radius: 50px;*/
  box-shadow:  5px 5px 10px rgba(153, 27, 27, .4),
  -5px -5px 10px rgba(254, 202, 202, .6);
}

.shadow-light:hover {
  /*border-radius: 50px;*/
  box-shadow: inset 5px 5px 10px rgba(153, 27, 27, .4),
  inset -5px -5px 10px rgba(254, 202, 202, .6);
}

.shadow-light:active {
  /*border-radius: 50px;*/
  box-shadow: inset 5px 5px 10px rgba(153, 27, 27, .4),
  inset -5px -5px 10px rgba(254, 202, 202, .6);
}

/*------------------------------------------------------------*/

.shadow-dark {
  /*border-radius: 50px;*/
  box-shadow:  5px 5px 10px rgba(153, 27, 27, .4),
  -5px -5px 10px rgba(254, 202, 202, .6);
}

.shadow-dark:hover {
  /*border-radius: 50px;*/
  box-shadow: inset 5px 5px 10px rgba(153, 27, 27, .4),
  inset -5px -5px 10px rgba(254, 202, 202, .6);
}

.shadow-dark:active {
  /*border-radius: 50px;*/
  box-shadow: inset 5px 5px 10px rgba(153, 27, 27, .4),
  inset -5px -5px 10px rgba(254, 202, 202, .6);
}

.nav-item {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  font-size: 1.875rem; /* 30px */
  line-height: 2.25rem; /* 36px */
  padding-left: 2.00rem; /* 4px */
  padding-right: 2.00rem; /* 4px */
}

#home-canvas{
  width: 50%;
  height: 100%;
}

.carousel > div{
  transform-origin: top left;
  transform: translateX(-30%) translateY(-50%);
}

.carousel > div:nth-child(1).inactive{
  border-radius: 0.75rem;
  background-color: black;
  transform: scale(80%) translateX(-70%) translateY(-50%);
  z-index: 20;
}

.carousel > div:nth-child(2).inactive{
  border-radius: 0.75rem;
  background-color: black;
  transform: scale(60%) translateX(-125%) translateY(-50%);
  z-index: 10;
}

.carousel > div.active{
  z-index: 30;
  border-radius: 0.75rem;
  transform: translateX(-30%) translateY(-50%);
  /*--------------------------*/
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
  /*--------------------------*/
  animation-name: carousel-active;
  animation-direction: normal;
  animation-duration: 0.5s;
  animation-fill-mode: none;
  animation-play-state: running;
  animation-timing-function: linear;
  animation-iteration-count: 1;
}

.carousel > div.active:hover{
  z-index: 30;
  transform: translateX(-30%) translateY(-52%);
}

@keyframes carousel-active {
  0%{
    transform: rotateY(40deg) translateX(40%) scale(70%);
  }

  100%{
    transform: rotateY(0deg) translateX(0%);
  }
}
